<template>
  <div class="right-module hot-blogs">
    <span>Hot Blogs</span>
    <ul>
      <li v-for="blog in blogTitles" :key="blog.id">
        <router-link :to="blog.link">{{blog.title}} | Page Views: {{blog.views}}</router-link>
      </li>
    </ul>
  </div>
</template>
<script>
let axios = require("axios");
export default {
  data() {
    return {
      blogTitles: []
    };
  },
  created() {
    axios({
      url: "/getHotBlogs?limit=3",
      method: "get"
    }).then(resp => {
      // console.log(resp.data.data);
      for (let i = 0; i < resp.data.data.length; i++) {
        resp.data.data[i].link = "blog_detail?bid=" + resp.data.data[i].id;
      }
      this.blogTitles = resp.data.data;
    });
  }
};
</script>
<style scoped>
.hot-blogs {
  margin-top: 20px;
}
.hot-blogs > span {
  font-weight: 700;
  display: block;
  width: 100%;
  height: 40px;
  color: #eee;
  line-height: 40px;
  padding: 0px 0px 0px 10px;
  box-sizing: border-box;
}

.hot-blogs li {
  height: 20px;
  border-bottom: 1px solid gray;
  padding: 10px 0px 5px 10px;
}
.hot-blogs li a {
  color: #008c8c;
  font-size: 14px;
}
.hot-blogs li a:hover {
  color: #eee;
}
</style>